body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  margin: 50px auto 0px auto;
  padding: 0px;
  width: 90%;
}

.input {
  margin-bottom: 20px;
}

.input-field {
  width: 100%;
  box-sizing: border-box;
  height: 30px;
  padding: 20px 20px;
  font-size: 20px;
}
.name {
    text-align: center;
    font-size: 35px;
    background-color: #f4f4f4;
    height: 200px;
    line-height: 200px;
}
.button {
    font-size: 20px;
    width: 200px;
    height: 50px;
    padding: 10px 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -70px 0px -0px -100px;
}
.results {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 70%;
    margin: 100px auto 0px;
}

.score {
    text-align: center;
    margin-top: 100px;
    background-color: #f4f4f4;
    border: 2px solid black;
    height: 200px;
    line-height: 200px;
    border-radius: 100%;
    width: 200px;
    font-size: 40px;
    margin: 10px;
}
